
const ServerUrl=function(){
	
	let ip=window.location.href.substring(0,window.location.href.indexOf("."));
		let url;
		if(ip == '59'){
			url="http://59.110.26.156:8091";
		}else if(ip == '101'){
			url="http://101.200.232.210:8020";
		}else{
			url="http://59.110.26.156:8091";
		}
		console.log(ip);
		console.log(url);
		return url;
	
}();
export default{
	content:ServerUrl,
}



